"use client";

import React from "react";
import { motion } from "framer-motion";
import { FaCubes, FaSearch, FaBalanceScale } from "react-icons/fa";
import { BiRupee } from "react-icons/bi";

const leftFeatures = [
  {
    title: "Passion-Driven Mission",
    desc: "Our strategy is based on responsibility, openness, and teamwork. We think that understanding and empathy are the first steps toward true change.",
    icon: FaCubes,
  },
  {
    title: "Transparency",
    desc: "Our NGO prioritizes transparency through regular public reporting, financial accountability, and open communication.",
    icon: FaSearch,
  },
];

const rightFeatures = [
  {
    title: "Accountability",
    desc: "Our NGO upholds accountability through transparent financial practices, regular reporting, and a commitment to ethical conduct, ensuring responsible and trustworthy operations.",
    icon: FaBalanceScale,
  },
  {
    title: "Fundraising for Impact",
    desc: "Our NGO focuses on impactful fundraising efforts, channeling resources to make a meaningful difference in our mission and create positive outcomes.",
    icon: BiRupee,
  },
];

export default function WhyChooseUsSection() {
  return (
    <section className="py-16 bg-white">
      <div className="container mx-auto px-4 lg:px-12">
        
        <div className="text-center max-w-3xl mx-auto mb-12">
          <h5 className="text-green-600 font-bold uppercase text-xl lg:text-2xl mb-3">
            Why CareFoundation Trust
          </h5>
          <h1 className="text-2xl md:text-3xl lg:text-4xl font-bold text-gray-800 mb-2">
            Our Work Promise To Uphold The Trust Placed
          </h1>
          <div className="w-24 h-1 bg-green-600 mx-auto rounded-full"></div>
        </div>

        <div className="grid grid-cols-1 lg:grid-cols-3 gap-8 lg:gap-12 items-center">
          
          <div className="space-y-10">
            {leftFeatures.map((feature, idx) => {
              const Icon = feature.icon;
              return (
                <motion.div
                  key={idx}
                  initial={{ opacity: 0, y: 50 }}
                  whileInView={{ opacity: 1, y: 0 }}
                  transition={{ delay: idx * 0.2, duration: 0.6 }}
                  viewport={{ once: true }}
                  className="flex flex-col items-center text-center lg:items-start lg:text-left"
                >
                  <div className="bg-green-600 rounded-full flex items-center justify-center w-16 h-16 mb-4 text-white text-xl shadow-lg">
                    <Icon />
                  </div>
                  <h4 className="text-lg md:text-xl font-bold text-gray-800 mb-3">
                    {feature.title}
                  </h4>
                  <p className="text-gray-600 text-sm md:text-base leading-relaxed">
                    {feature.desc}
                  </p>
                </motion.div>
              );
            })}
          </div>

          <motion.div
            initial={{ opacity: 0, y: 50 }}
            whileInView={{ opacity: 1, y: 0 }}
            transition={{ delay: 0.2, duration: 0.6 }}
            viewport={{ once: true }}
            className="relative h-96 w-full flex justify-center items-center rounded-2xl overflow-hidden shadow-2xl"
          >
            <img
              src="/images/whychooseus.webp" 
              alt="Why Choose Us - Helping those in need"
              className="h-full w-full object-cover"
            />
            {/* Overlay text on image */}
            <div className="absolute bottom-0 left-0 right-0 p-6 bg-gradient-to-t from-black/80 to-transparent">
              <p className="text-white font-bold text-xl lg:text-2xl mb-2 text-center">
                GIVE SOMETHING TO THOSE WHO HAVE NOTHING
              </p>
              <button className="block mx-auto bg-green-600 hover:bg-green-700 text-white px-6 py-2 rounded-lg font-semibold transition-all duration-300 transform hover:scale-105">
                DONATE NOW
              </button>
            </div>
          </motion.div>

          <div className="space-y-10">
            {rightFeatures.map((feature, idx) => {
              const Icon = feature.icon;
              return (
                <motion.div
                  key={idx}
                  initial={{ opacity: 0, y: 50 }}
                  whileInView={{ opacity: 1, y: 0 }}
                  transition={{ delay: idx * 0.2, duration: 0.6 }}
                  viewport={{ once: true }}
                  className="flex flex-col items-center text-center lg:items-start lg:text-left"
                >
                  <div className="bg-green-600 rounded-full flex items-center justify-center w-16 h-16 mb-4 text-white text-xl shadow-lg">
                    <Icon />
                  </div>
                  <h4 className="text-lg md:text-xl font-bold text-gray-800 mb-3">
                    {feature.title}
                  </h4>
                  <p className="text-gray-600 text-sm md:text-base leading-relaxed">
                    {feature.desc}
                  </p>
                </motion.div>
              );
            })}
          </div>
        </div>

        {/* WhatsApp CTA */}
        <motion.div
          initial={{ opacity: 0, y: 20 }}
          whileInView={{ opacity: 1, y: 0 }}
          viewport={{ once: true }}
          transition={{ delay: 0.5, duration: 0.6 }}
          className="mt-12 text-center"
        >
          <button
            onClick={() => window.open("https://wa.me/919867491052", "_blank")}
            className="bg-green-600 hover:bg-green-700 text-white font-semibold px-8 py-4 rounded-xl shadow-lg hover:shadow-xl transition-all duration-300 transform hover:scale-105 flex items-center gap-3 mx-auto"
          >
            <svg className="w-6 h-6" fill="currentColor" viewBox="0 0 24 24">
              <path d="M17.472 14.382c-.297-.149-1.758-.867-2.03-.967-.273-.099-.471-.148-.67.15-.197.297-.767.966-.94 1.164-.173.199-.347.223-.644.075-.297-.15-1.255-.463-2.39-1.475-.883-.788-1.48-1.761-1.653-2.059-.173-.297-.018-.458.13-.606.134-.133.298-.347.446-.52.149-.174.198-.298.298-.497.099-.198.05-.371-.025-.52-.075-.149-.669-1.612-.916-2.207-.242-.579-.487-.5-.669-.51-.173-.008-.371-.01-.57-.01-.198 0-.52.074-.792.372-.272.297-1.04 1.016-1.04 2.479 0 1.462 1.065 2.875 1.213 3.074.149.198 2.096 3.2 5.077 4.487.709.306 1.262.489 1.694.625.712.227 1.36.195 1.871.118.571-.085 1.758-.719 2.006-1.413.248-.694.248-1.289.173-1.413-.074-.124-.272-.198-.57-.347m-5.421 7.403h-.004a9.87 9.87 0 01-5.031-1.378l-.361-.214-3.741.982.998-3.648-.235-.375a9.86 9.86 0 01-1.51-5.26c.001-5.45 4.436-9.884 9.888-9.884 2.64 0 5.122 1.03 6.988 2.898a9.825 9.825 0 012.893 6.994c-.003 5.45-4.437 9.884-9.885 9.884m8.413-18.297A11.815 11.815 0 0012.05 0C5.495 0 .16 5.335.157 11.892c0 2.096.547 4.142 1.588 5.945L.057 24l6.305-1.654a11.882 11.882 0 005.683 1.448h.005c6.554 0 11.89-5.335 11.893-11.893a11.821 11.821 0 00-3.48-8.413Z"/>
            </svg>
            <span className="text-lg">Start A Fundraiser On Whatsapp</span>
          </button>
        </motion.div>
      </div>
    </section>
  );
}


