"use client";
import { useEffect, useState } from "react";
import { motion } from "framer-motion";
import Image from "next/image";
import { useRouter } from "next/navigation";
import { FaHeart } from "react-icons/fa";
import { campaignService } from "@/services/campaignService";

export default function LatestFundraisersSection() {
  const router = useRouter();
  const [latestCampaigns, setLatestCampaigns] = useState([]);
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    const fetchLatestCampaigns = async () => {
      try {
        const response = await campaignService.getCampaigns({ limit: 3, status: 'active', sortBy: 'createdAt', sortOrder: 'desc' });
        if (response.status === 'success' && response.data) {
          const campaigns = Array.isArray(response.data) ? response.data : response.data.campaigns || [];
          setLatestCampaigns(campaigns.slice(0, 3));
        }
      } catch (error) {
        // Silently handle network errors (backend might not be running)
        if (error.code === 'ERR_NETWORK' || error.message?.includes('Network Error')) {
          // Backend not available - silently fail
        } else {
          console.error("Failed to fetch latest campaigns:", error);
        }
      } finally {
        setIsLoading(false);
      }
    };

    fetchLatestCampaigns();
  }, []);

  if (isLoading) {
    return (
      <section className="py-16 bg-white">
        <div className="container mx-auto px-6">
          <div className="text-center mb-12">
            <h2 className="text-3xl font-bold text-green-600">Loading...</h2>
          </div>
        </div>
      </section>
    );
  }

  if (latestCampaigns.length === 0) {
    return null;
  }

  return (
    <section className="py-16 bg-white">
      <div className="container mx-auto px-6 lg:px-12">
        {/* Section Header */}
        <motion.div
          initial={{ opacity: 0, y: 20 }}
          whileInView={{ opacity: 1, y: 0 }}
          viewport={{ once: true }}
          transition={{ duration: 0.6 }}
          className="text-center mb-12"
        >
          <h2 className="text-3xl lg:text-4xl font-bold text-green-600 mb-4">
            LATEST 3 FUNDRAISER
          </h2>
          <div className="w-24 h-1 bg-green-600 mx-auto rounded-full"></div>
        </motion.div>

        {/* Campaigns Grid */}
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-8">
          {latestCampaigns.map((campaign, index) => {
            const progress = Math.min(
              ((campaign.currentAmount || 0) / (campaign.goalAmount || 1)) * 100,
              100
            );

            return (
              <motion.div
                key={campaign._id}
                initial={{ opacity: 0, y: 40 }}
                whileInView={{ opacity: 1, y: 0 }}
                whileHover={{ y: -10, scale: 1.02 }}
                viewport={{ once: true }}
                transition={{ delay: index * 0.2, duration: 0.6 }}
                className="bg-white rounded-2xl shadow-lg hover:shadow-2xl transition-all duration-300 overflow-hidden border-2 border-green-100 hover:border-green-400 cursor-pointer group"
                onClick={() => router.push(`/campaigns/${campaign._id}`)}
              >
                {/* Image Section */}
                <div className="relative h-64 overflow-hidden">
                  <motion.div
                    whileHover={{ scale: 1.1 }}
                    transition={{ duration: 0.5 }}
                  >
                    <Image
                      src={campaign.images?.[0]?.url || "/images/c1.jpg"}
                      alt={campaign.title}
                      fill
                      className="object-cover transition-transform duration-500"
                    />
                  </motion.div>
                  <div className="absolute inset-0 bg-gradient-to-t from-black/60 via-transparent to-transparent group-hover:from-black/70 transition-all duration-300"></div>
                  
                  {/* Tax Benefits Badge */}
                  {campaign.isTaxDeductible && (
                    <div className="absolute top-4 left-4 bg-green-600 text-white px-3 py-1 rounded-full text-xs font-bold shadow-lg">
                      Tax Benefits
                    </div>
                  )}
                </div>

                {/* Content Section */}
                <div className="p-6">
                  {/* Title */}
                  <h3 className="text-lg font-bold text-gray-900 mb-4 line-clamp-2 min-h-[56px]">
                    {campaign.title}
                  </h3>

                  {/* Progress Bar */}
                  <div className="mb-4">
                    <div className="flex justify-between text-sm mb-2">
                      <span className="font-bold text-gray-900">
                        ₹{(campaign.currentAmount || 0).toLocaleString("en-IN")}/- Raised Out Of ₹{(campaign.goalAmount || 0).toLocaleString("en-IN")}
                      </span>
                    </div>
                    <div className="w-full bg-gray-200 h-3 rounded-full overflow-hidden">
                      <motion.div
                        initial={{ width: 0 }}
                        whileInView={{ width: `${progress}%` }}
                        viewport={{ once: true }}
                        transition={{ duration: 1, ease: "easeOut" }}
                        className="h-full bg-gradient-to-r from-green-500 to-green-600 rounded-full"
                      />
                    </div>
                  </div>

                  {/* CTA Button */}
                  <button
                    onClick={(e) => {
                      e.stopPropagation();
                      router.push(`/campaigns/${campaign._id}`);
                    }}
                    className="w-full bg-gradient-to-r from-green-600 to-green-700 hover:from-green-700 hover:to-green-800 text-white font-bold py-3 px-4 rounded-lg shadow-md transition-all duration-300 transform hover:scale-105 flex items-center justify-center gap-2"
                  >
                    <FaHeart />
                    <span>CONTRIBUTE NOW❤</span>
                  </button>
                </div>
              </motion.div>
            );
          })}
        </div>
      </div>
    </section>
  );
}

