"use client";
import { useState } from "react";
import { motion } from "framer-motion";
import { useRouter } from "next/navigation";
import { FaBuilding, FaQrcode, FaRupeeSign, FaWhatsapp } from "react-icons/fa";
import toast from "react-hot-toast";
import Image from "next/image";

export default function DonateNowSection() {
  const router = useRouter();
  const [donationAmount, setDonationAmount] = useState("");

  const handleAmountSelect = (amount) => {
    setDonationAmount(amount.toString());
  };

  const handleDonate = () => {
    if (!donationAmount || parseFloat(donationAmount) < 10) {
      toast.error("Please select or enter a minimum amount of ₹10");
      return;
    }
    // Redirect to campaigns page with amount
    router.push(`/campaigns?donate=${donationAmount}`);
  };

  const bankDetails = {
    accountNumber: "50200068627799",
    accountName: "Care Foundation Trust",
    ifsc: "HDFC0000626",
    bankName: "HDFC Bank"
  };

  return (
    <section className="py-16 bg-gradient-to-br from-gray-50 to-white">
      <div className="container mx-auto px-6 lg:px-12">
        
        {/* Section Header */}
        <motion.div
          initial={{ opacity: 0, y: 20 }}
          whileInView={{ opacity: 1, y: 0 }}
          viewport={{ once: true }}
          transition={{ duration: 0.6 }}
          className="text-center mb-12"
        >
          <h2 className="text-2xl lg:text-3xl font-bold text-gray-800 mb-2">
            DONATE NOW
          </h2>
          <h3 className="text-xl lg:text-2xl font-semibold text-gray-700">
            Donate For The People And Causes You Care About
          </h3>
          <div className="w-24 h-1 bg-green-600 mx-auto rounded-full mt-4"></div>
        </motion.div>

        <div className="grid grid-cols-1 lg:grid-cols-2 gap-12 items-start">
          
          {/* Left Section - Donation Form */}
          <motion.div
            initial={{ opacity: 0, x: -30 }}
            whileInView={{ opacity: 1, x: 0 }}
            viewport={{ once: true }}
            transition={{ duration: 0.6 }}
            className="space-y-6"
          >
            {/* Description */}
            <div className="flex items-start gap-3">
              <div className="text-green-600 text-2xl mt-1">
                <FaRupeeSign />
              </div>
              <p className="text-gray-700 text-lg">
                Every ₹ you donate goes to the community. Care Foundation Trust is a non-profit organisation committed to promoting compassion, empathy, addressing critical social issues, implementing sustainable solutions, and uplifting underprivileged and marginalised communities.
              </p>
            </div>

            {/* Contact Info */}
            <div className="flex items-center gap-3 text-gray-700">
              <FaWhatsapp className="text-green-600 text-xl" />
              <span className="font-semibold">Direct Message Us:</span>
              <a 
                href="https://wa.me/917304786365" 
                target="_blank" 
                rel="noopener noreferrer"
                className="text-green-600 hover:underline font-bold"
              >
                +91 7304786365
              </a>
            </div>

            {/* Donation Form */}
            <div className="bg-green-50 rounded-2xl p-8 shadow-lg">
              <label className="block text-lg font-semibold text-gray-800 mb-4">
                Amount To Donate (₹)
              </label>
              
              {/* Quick Amount Buttons */}
              <div className="grid grid-cols-2 gap-4 mb-6">
                {[100, 500, 1000, 2000].map((amount, idx) => (
                  <motion.button
                    key={amount}
                    onClick={() => handleAmountSelect(amount)}
                    initial={{ opacity: 0, scale: 0.8 }}
                    whileInView={{ opacity: 1, scale: 1 }}
                    viewport={{ once: true }}
                    transition={{ delay: idx * 0.1, duration: 0.3 }}
                    whileHover={{ scale: 1.05, y: -2 }}
                    whileTap={{ scale: 0.95 }}
                    className={`py-4 px-6 rounded-xl font-bold text-lg transition-all duration-300 ${
                      donationAmount === amount.toString()
                        ? "bg-green-600 text-white shadow-lg ring-4 ring-green-200"
                        : "bg-white text-gray-700 hover:bg-green-100 border-2 border-gray-200 hover:border-green-400"
                    }`}
                  >
                    ₹{amount}
                  </motion.button>
                ))}
              </div>

              {/* Custom Amount Input */}
              <div className="mb-6">
                <input
                  type="number"
                  value={donationAmount}
                  onChange={(e) => setDonationAmount(e.target.value)}
                  placeholder="Enter Custom Amount"
                  min="10"
                  className="w-full px-4 py-3 rounded-xl border-2 border-gray-300 focus:border-green-500 focus:ring-2 focus:ring-green-200 outline-none text-lg font-semibold"
                />
                <p className="text-sm text-gray-600 mt-2">Minimum: ₹10</p>
              </div>

              {/* Donate Button */}
              <motion.button
                onClick={handleDonate}
                whileHover={{ scale: 1.05, y: -2 }}
                whileTap={{ scale: 0.95 }}
                className="w-full bg-gray-800 hover:bg-gray-900 text-white font-bold py-4 rounded-xl shadow-lg transition-all duration-300"
              >
                Donate
              </motion.button>
            </div>
          </motion.div>

          {/* Right Section - Bank Details */}
          <motion.div
            initial={{ opacity: 0, x: 30 }}
            whileInView={{ opacity: 1, x: 0 }}
            viewport={{ once: true }}
            transition={{ duration: 0.6 }}
            className="bg-white rounded-2xl p-8 shadow-xl border-2 border-green-100"
          >
            <div className="flex items-center gap-3 mb-6">
              <FaBuilding className="text-green-600 text-3xl" />
              <h3 className="text-2xl font-bold text-gray-800">
                If You Want To Donate via Account Details, Use these Details
              </h3>
            </div>

            <div className="space-y-4 mb-6">
              <div className="flex justify-between items-center py-2 border-b border-gray-200">
                <span className="font-semibold text-gray-700">Bank A/C No:</span>
                <span className="font-bold text-gray-900">{bankDetails.accountNumber}</span>
              </div>
              <div className="flex justify-between items-center py-2 border-b border-gray-200">
                <span className="font-semibold text-gray-700">Bank A/C Name:</span>
                <span className="font-bold text-gray-900">{bankDetails.accountName}</span>
              </div>
              <div className="flex justify-between items-center py-2 border-b border-gray-200">
                <span className="font-semibold text-gray-700">Branch IFSC:</span>
                <span className="font-bold text-gray-900">{bankDetails.ifsc}</span>
              </div>
              <div className="flex justify-between items-center py-2 border-b border-gray-200">
                <span className="font-semibold text-gray-700">Bank Name:</span>
                <span className="font-bold text-gray-900">{bankDetails.bankName}</span>
              </div>
            </div>

            {/* OR Divider */}
            <div className="flex items-center gap-4 my-6">
              <div className="flex-1 h-px bg-gray-300"></div>
              <span className="font-semibold text-gray-600">Or</span>
              <div className="flex-1 h-px bg-gray-300"></div>
            </div>

            {/* QR Code Section */}
            <div className="text-center">
              <div className="flex items-center gap-2 justify-center mb-4">
                <FaQrcode className="text-green-600 text-2xl" />
                <p className="font-semibold text-gray-700">
                  Scan QR by any UPI App to make donations for any Noble Cause
                </p>
              </div>
              
              {/* Static QR Code */}
              <motion.div
                initial={{ opacity: 0, scale: 0.9 }}
                whileInView={{ opacity: 1, scale: 1 }}
                viewport={{ once: true }}
                transition={{ duration: 0.5 }}
                className="bg-white rounded-xl p-6 flex justify-center items-center border-2 border-green-200 shadow-lg hover:shadow-2xl transition-all duration-300"
              >
                <div className="text-center">
                  {/* QR Code Image - Static Design */}
                  <motion.div
                    whileHover={{ scale: 1.05 }}
                    transition={{ duration: 0.3 }}
                    className="bg-white p-4 rounded-lg inline-block border-4 border-green-600 shadow-xl"
                  >
                    <div className="w-[220px] h-[220px] bg-white relative overflow-hidden rounded">
                      {/* QR Code Pattern - Static Design */}
                      <div className="absolute inset-2 bg-black rounded">
                        {/* Corner squares */}
                        <div className="absolute top-2 left-2 w-14 h-14 border-4 border-black rounded-tl-lg"></div>
                        <div className="absolute top-2 left-2 w-10 h-10 border-2 border-white rounded-tl-md"></div>
                        
                        <div className="absolute top-2 right-2 w-14 h-14 border-4 border-black rounded-tr-lg"></div>
                        <div className="absolute top-2 right-2 w-10 h-10 border-2 border-white rounded-tr-md"></div>
                        
                        <div className="absolute bottom-2 left-2 w-14 h-14 border-4 border-black rounded-bl-lg"></div>
                        <div className="absolute bottom-2 left-2 w-10 h-10 border-2 border-white rounded-bl-md"></div>
                        
                        {/* Pattern blocks */}
                        <div className="absolute top-20 left-6 w-2 h-16 bg-black"></div>
                        <div className="absolute top-24 left-10 w-12 h-2 bg-black"></div>
                        <div className="absolute top-28 left-10 w-8 h-2 bg-black"></div>
                        
                        <div className="absolute top-20 right-6 w-2 h-16 bg-black"></div>
                        <div className="absolute top-24 right-10 w-12 h-2 bg-black"></div>
                        
                        <div className="absolute bottom-20 left-6 w-2 h-16 bg-black"></div>
                        <div className="absolute bottom-24 left-10 w-12 h-2 bg-black"></div>
                        
                        {/* Random pattern blocks */}
                        <div className="absolute top-32 left-20 w-3 h-3 bg-black"></div>
                        <div className="absolute top-40 left-28 w-4 h-4 bg-black"></div>
                        <div className="absolute top-48 left-16 w-2 h-6 bg-black"></div>
                        <div className="absolute top-56 left-24 w-6 h-2 bg-black"></div>
                        
                        <div className="absolute bottom-32 right-20 w-3 h-3 bg-black"></div>
                        <div className="absolute bottom-40 right-28 w-4 h-4 bg-black"></div>
                        <div className="absolute bottom-48 right-16 w-2 h-6 bg-black"></div>
                        <div className="absolute bottom-56 right-24 w-6 h-2 bg-black"></div>
                        
                        {/* Center logo area */}
                        <div className="absolute inset-0 flex items-center justify-center">
                          <div className="bg-white rounded-lg p-3 border-2 border-black">
                            <FaQrcode className="text-black text-2xl" />
                          </div>
                        </div>
                      </div>
                    </div>
                  </motion.div>
                  <motion.p
                    initial={{ opacity: 0 }}
                    whileInView={{ opacity: 1 }}
                    viewport={{ once: true }}
                    transition={{ delay: 0.3 }}
                    className="text-gray-700 text-sm mt-4 font-semibold"
                  >
                    Scan with any UPI App
                  </motion.p>
                  <p className="text-gray-600 text-xs mt-1">
                    Pay to: Care Foundation Trust
                  </p>
                  <p className="text-gray-500 text-xs mt-1 font-medium">
                    Account: 50200068627799
                  </p>
                </div>
              </motion.div>
            </div>
          </motion.div>
        </div>

        {/* WhatsApp CTA */}
        <motion.div
          initial={{ opacity: 0, y: 20 }}
          whileInView={{ opacity: 1, y: 0 }}
          viewport={{ once: true }}
          transition={{ delay: 0.3, duration: 0.6 }}
          className="mt-12 text-center"
        >
          <button
            onClick={() => window.open("https://wa.me/919867491052", "_blank")}
            className="bg-green-600 hover:bg-green-700 text-white font-semibold px-8 py-4 rounded-xl shadow-lg hover:shadow-xl transition-all duration-300 transform hover:scale-105 flex items-center gap-3 mx-auto"
          >
            <FaWhatsapp className="text-2xl" />
            <span className="text-lg">Start A Fundraiser On Whatsapp</span>
          </button>
        </motion.div>
      </div>
    </section>
  );
}

